.rules { 
    padding-top: 20px;
    text-align: center;
    user-select: none;
    &__box {
        margin: 0 auto;
        margin-top: 10px;
        width: 500px;
        height: 400px;
        object-fit: cover;
        user-select: none;
        pointer-events: none;
        img {
            width: 100%;
            height: 100%; 
            user-select: none;
            pointer-events: none;
        }
    }
    &__exit {
        position: absolute;
        top: 10px;
        left: 20px;
    }
}